.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/*  */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: hsl(253, 22%, 21%) !important;
}

h3 {
  background-color: lightgray;
  width: 450px;
  height: 75px;
  margin: 25px auto;
  border-radius: 10px;
  font-size: 50px;
}

span {
  display: inline-block;
  height: 50px;
  width: 450px;
  color: gray;
  font-size: 32px;
  margin: 15px 0;
}

.panel {
  width: 100%;
  display: flex;
  height: 300px;
  margin: auto;
  padding: 0;
  justify-content: center;
}

.numbers {
  display: flex;
  width: 225px;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

button {
  font-size: 24px;
}

.numbers button {
  width: 75px;
  height: 75px;
}

.operators {
  display: flex;
  width: 60px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* flex-wrap: wrap; */
}

.operators button {
  width: 60px;
  height: 60px;
}


@media only screen and (max-width: 600px){
  h3 {
    width: 250px;
    height: 50px;
    font-size: 30px;
  }

  span {
    font-size: 32px;
    width: 100%;
    text-align: center !important;
    /* background-color: red; */
  }
}